import React, {useState} from "react";
import { ArrowForward, ArrowRight, HeroBg, HeroBtnWrapper, HeroContainer, HeroContent, HeroH1, HeroP, VideoBg } from "./HeroElements";
import Video from "../../assets/videos/video.mp4";
import { Button } from "../CustomStyles";

const HeroSection = () => {
    const [hover, setHover]=useState(false)
    const onHover = () => {
        setHover(!hover)
    }
  return (
    <HeroContainer id="home">
      <HeroBg>
        <VideoBg autoPlay loop muted src={Video} type="video/mp4" />
      </HeroBg>
      <HeroContent>
        <HeroH1>Pablo Zarate</HeroH1>
        <HeroP>Fullstack Developer</HeroP>
        <HeroBtnWrapper>
          <Button onMouseEnter={onHover} to="singup">
            Get Started
            {hover ? <ArrowForward /> : <ArrowRight />}
          </Button>
        </HeroBtnWrapper>
      </HeroContent>
    </HeroContainer>
  );
};

export default HeroSection;
